/*
// .th-mobile-indicator
*/

$local-open-transition-duration: .2s;

.th-mobile-indicator {
    position: relative;
}
.th-mobile-indicator__button {
    padding: 0;
    display: flex;
    align-items: center;
    position: relative;
    justify-content: center;
    height: 50px;
    width: 44px;
    color: inherit;
    background: transparent;
    border: none;

    &:focus {
        outline: none;
    }
    &:hover {
        color: inherit;
        text-decoration: none;
    }
}
.th-mobile-indicator__icon {
    display: block;
    position: relative;

    svg {
        display: block;

        @include direction {
            transform: scaleX($transform-direction); // SKIP-RTL
        }
    }
}
.th-mobile-indicator__counter {
    position: absolute;
    top: -7px;

    @include direction {
        #{$inset-inline-end}: -7px;
    }

    @include th-indicator;
}
.th-mobile-indicator__title,
.th-mobile-indicator__value {
    display: block;

    @include direction {
        #{$margin-inline-start}: 50px;
    }
}
.th-mobile-indicator__title {
    padding-top: 3px;
    margin-bottom: -3px;
    font-size: 13px;
}
.th-mobile-indicator__value {
    font-size: 18px;
    font-weight: $font-weight-medium;
}


@mixin local-define-scheme($scheme) {
    .th-mobile-indicator__icon {
        fill: map_get($scheme, opposite);
    }
    .th-mobile-indicator__title {
        color: map_get($scheme, muted);
    }
    .th-mobile-indicator__value {
        @include scheme-font-weight($scheme, opposite, $font-weight-medium);
    }

    .th-mobile-indicator:hover {
        .th-mobile-indicator__button {
            background: map_get($scheme, hover);
        }
    }
}


// define scheme
@include local-define-scheme($mobile-header-scheme);

.th-mobile-indicator__counter {
    color: map_get($mobile-indicator-counter-scheme, opposite);

    @include scheme-font-weight($mobile-indicator-counter-scheme, opposite, $font-weight-medium);

    &:before {
        background-color: map_get($mobile-indicator-counter-scheme, main);
    }
}
