@mixin th-modal-backdrop {
    position: fixed;
    left: 0; // SKIP-RTL
    top: 0;
    right: 0; // SKIP-RTL
    bottom: 0;
    z-index: 1000;
    background: $modal-backdrop;
    opacity: 0;
    will-change: opacity;
    transition: opacity $local-transition-duration $local-transition-timing-function;
}
@mixin th-modal-sidebar-body {
    position: fixed;
    top: 0;
    bottom: 0;
    z-index: 1000;
    background: map_get($card-scheme, main);
    width: 300px;
    transition: transform $local-transition-duration $local-transition-timing-function;
    will-change: transform;
    overflow: hidden;
    padding-top: var(--th-admin-bar-height);

    @include direction {
        #{$inset-inline-start}: 0;
        transform: translateX(#{-100% * $transform-direction}); // SKIP-RTL
    }
}
@mixin th-modal-close-button {
    z-index: 2;
    position: absolute;
    padding: 16px;
    border: none;
    top: 0;
    border-radius: 0 2.5px 0 2.5px;
    background: transparent;
    fill: currentColor;
    transition:
        background .2s,
        color .2s;

    @include define-button-scheme($btn-muted-scheme);
    @include direction {
        #{$inset-inline-end}: 0;
    }

    svg {
        display: block;
    }

    &:active {
        transition-duration: 0s;
    }
    &:focus {
        outline: none;
    }
}
