/*
// .th-logo--mobile
*/

.th-logo--mobile {
    position: relative;

    .th-logo__image {
        align-items: center;
        height: 50px;
    }
    .th-logo__title {
        line-height: 1;
        font-size: 28px;
        font-weight: bold;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 50px;
        text-align: center;
        margin: 0;
        flex-grow: 1;
        text-transform: uppercase;
    }

    a,
    a:hover {
        color: inherit;
    }

    a:before {
        position: absolute;
        content: '';
        display: block;
        left: 0; // SKIP-RTL
        right: 0; // SKIP-RTL
        top: 0;
        bottom: 0;
    }

    img {
        display: block;
    }

    &.th-logo--has-mobile-image,
    .wp-custom-logo & {
        .th-logo__image {
            display: flex;
        }
        .th-logo__title {
            display: none;
        }
    }
    &.th-logo--has-mobile-image {
        .th-logo__image--desktop {
            display: none;
        }
    }

    // define scheme
    svg {
        display: block;
        fill: currentColor;
    }

    .th-part-primary {
        color: map_get($logo-scheme, primary);
    }
    .th-part-secondary {
        color: map_get($logo-scheme, secondary);
    }

    .th-logo__image {
        img,
        svg {
            max-height: 50px;
            max-width: 180px;
            height: auto;
            width: auto;
        }
    }
}
