/*
// .th-search--location--mobile-header
*/

$local-height: 36px;

.th-search--location--mobile-header {
    .th-search__wrapper {
        width: 100%;
    }
    .th-search__body {
        display: flex;
        position: relative;
        z-index: 0;
        width: 100%;
    }
    .th-search__button svg {
        display: block;
    }
    .th-search__input,
    .th-search__button--search,
    .th-search__button--close {
        background: transparent;
        border: none;
        font-family: inherit;
        color: inherit;

        &:focus {
            outline: none;
        }
    }
    .th-search__input {
        flex-grow: 1;
        font-size: 15px;
        height: auto;
        min-height: auto;
    }
    .th-search__box {
        position: absolute;
        left: 0; // SKIP-RTL
        top: 0;
        right: 0; // SKIP-RTL
        bottom: 0;
        pointer-events: none;
        z-index: -1;
        transition:
            color .2s,
            background .2s,
            border-color .2s;
    }
    .th-search__button--search,
    .th-search__button--close {
        width: ($local-height + 2px);
        flex-shrink: 0;
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: color .2s;
        fill: currentColor;
    }
    .th-search__button--vehicle {
        order: -1;
        background: map_get($mobile-search-vehicle-button-scheme, normal-bg-color);
        color: map_get($mobile-search-vehicle-button-scheme, normal-font-color);
        min-width: 44px;
        padding: 0 12px;
        white-space: nowrap;
        font-size: 15px;
        font-weight: $font-weight-medium;
        border-radius: 2.5px;
        flex-shrink: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        border: none;
        transition:
            color .15s,
            background-color .15s;

        &:hover {
            background: map_get($mobile-search-vehicle-button-scheme, hover-bg-color);
        }
        &:active {
            background: map_get($mobile-search-vehicle-button-scheme, active-bg-color);
        }
        &:focus {
            outline: none;
        }

        svg {
            flex-shrink: 0;
        }

        .th-search__button-title {
            @include direction {
                #{$margin-inline-start}: 8px;
            };
        }

        .th-search__button-icon {
            fill: map_get($mobile-search-vehicle-button-scheme, normal-icon-color);
        }
    }
    .th-search__button--search .th-search__button-title {
        display: none;
    }


    @include media-breakpoint-up(md) {
        .th-search__input {
            height: $local-height;
            padding: 0 11px;
            border-radius: 2.5px;
            border-width: 1px;
            border-style: solid;
            transition:
                color .2s,
                background .2s,
                border-color .2s;

            @include define-input-scheme($mobile-search-scheme, normal);

            @include direction {
                #{$padding-inline-start}: 12px - 1px;
                #{$padding-inline-end}: 38px - 1px;
            }

            &:hover {
                @include define-input-scheme($mobile-search-scheme, hover);
            }
            &:focus {
                @include define-input-scheme($mobile-search-scheme, focus);
            }
        }
        .th-search__button--close {
            display: none;
        }
        .th-search__button--search {
            position: absolute;
            top: 0;
            height: 100%;

            @include direction {
                #{$inset-inline-end}: 0;
            }
        }
        .th-search__button--vehicle {
            @include direction {
                #{$margin-inline-end}: 6px;
            }
        }

        @each $state in (normal, focus, hover) {
            $selector: '&';

            @if ($state != normal) {
                $selector: '.th-search__input:' + $state + '~';
            }

            #{$selector} {
                $local-button-scheme: map-get-default-map($mobile-search-scheme, #{$state}-icon-scheme);

                .th-search__button--search {
                    color: map_get($local-button-scheme, normal-font-color);
                }
                .th-search__button--search:hover {
                    color: map_get($local-button-scheme, hover-font-color);
                }
            }
        }
    }
    @include media-breakpoint-only(md) {
        .th-search__button--vehicle .th-search__button-title {
            display: none;
        }
    }
    @include media-breakpoint-down(sm) {
        .th-search__wrapper,
        .th-search__body {
            height: 100%;
        }
        .th-search__input {
            padding-top: 0;
            padding-bottom: 0;
            flex-basis: 0;
            width: 0;

            @include direction {
                #{$padding-inline-start}: 12px;
                #{$padding-inline-end}: 0;
            }
        }
        .th-search__button--search,
        .th-search__button--close {
            width: 52px;
        }
        .th-search__button--close {
            border-width: 0;
            border-style: solid;

            @include direction {
                #{$border-inline-start}-width: 1px;
            }
        }

        @each $state in (normal, focus, hover) {
            $input-selector: '';
            $button-selector: '&';

            @if ($state != normal) {
                $input-selector: ':' + $state;
                $button-selector: '.th-search__input:' + $state + '~';
            }

            .th-search__input#{$input-selector} {
                color: map_get($mobile-search-overlay-scheme, #{$state}-font-color);

                &::placeholder {
                    color: map_get($mobile-search-overlay-scheme, #{$state}-placeholder-color);
                }

                & ~ .th-search__box {
                    background: map_get($mobile-search-overlay-scheme, #{$state}-bg-color);
                }

                & ~ .th-search__button--close {
                    border-color: map_get($mobile-search-overlay-scheme, #{$state}-border-color);
                }
            }

            #{$button-selector} {
                $local-button-scheme: map-get-default-map($mobile-search-overlay-scheme, #{$state}-icon-scheme);

                .th-search__button--search,
                .th-search__button--close {
                    color: map_get($local-button-scheme, normal-font-color);
                }
                .th-search__button--search:hover,
                .th-search__button--close:hover {
                    color: map_get($local-button-scheme, hover-font-color);
                }
            }
        }

        .th-search__button--vehicle {
            margin: 8px 0;

            @include direction {
                #{$margin-inline-start}: 8px;
            }
        }
    }
    @media (max-width: 440px) {
        .th-search__button--vehicle .th-search__button-title {
            display: none;
        }
    }
}
